import { Component, OnInit, Input, ViewChild, Compiler, NgModule, ViewContainerRef } from '@angular/core';
import { Slides } from 'ionic-angular';
import { AppServices } from '../../app/tester.service';
import { Page } from '../../app/page';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  providers: [AppServices]
})
export class HomePage implements OnInit {

  private _appService: AppServices;
  private json:any;
  private pages: Page[] = [];
  @Input() filesGroup: string;
  @ViewChild(Slides) slides: Slides;
  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  

  constructor(appService: AppServices, private compiler: Compiler ) {
    this._appService = appService;
  }

  private getJson() : void {
    this._appService.getJSON("file").subscribe(data => { 
      this.populateJson(data);
      this.populatePages();
    });
  }

  private populateJson(data: any): void {
    var self = this;
    this.json = data.find((obj:any) => {
      return obj.groupname === self.filesGroup;
    })
  }

  private populatePages() : void {
    for(let i = 0; i < this.json.pages.length; i++ ) {
       let newPage = new Page();
       newPage.name = this.json.pages[i].name;
       newPage.template = this.json.pages[i].template;
       this.pages.push(newPage);
    }
  }

  public goToNextPage() {
    this.slides.slideNext();
  }

  public goToPrevPage() {
    this.slides.slidePrev();
  }

  ngOnInit(): void {
    if(this.filesGroup){
      this.getJson();
    }
  }

}



